import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { setToken } from "../../reducers/login";
import { useAlert } from "../";
export function useLoginEffect(data, fulfilled, pending, rejected, error) {
  //   state
  const [state, setState] = useState({
    userId: "",
    password: "",
  });
  const dispatch = useDispatch();
  const [alertData, alertAction] = useAlert();
  //    useEffect
  useEffect(() => {
    if (fulfilled) {
      // response  
      const accessToken = data.accessToken;
      const refreshToken = data.refreshToken;

      //    
      window.sessionStorage.setItem("id", state.userId);
      window.sessionStorage.setItem("accessToken", accessToken);
      window.sessionStorage.setItem("refreshToken", refreshToken);
      alertAction.open(" ");

      //  
      const reduxData = {
        isToken: true,
        userId: state.userId,
      };
      dispatch(setToken(reduxData));
    }
  }, [fulfilled]);

  //  
  useEffect(() => {
    if (rejected) {
      if (error) {
        //   
        alertAction.open(error.response.data);

        //   ,  
        setState({
          ...state,
          password: "",
        });
      }
    }
  }, [rejected]);

  // login page input       state 
  return [state, setState];
}

//      ,    
//     
export function useLoginAuth() {
  const dispatch = useDispatch();

  //  (  app   ) 
  useEffect(() => {
    //   
    let test = null;
    test = window.sessionStorage.getItem("accessToken");
    const id = window.sessionStorage.getItem("id");

    // null 
    if (test !== null) {
      // payload 
      const reduxData = {
        isToken: true,
        userId: id,
      };

      //  
      dispatch(setToken(reduxData));
    }
  }, []);
}
