import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

const useProfileRecruit = (
  getProjectData,
  getProjectFulfilled,
  getProjectRejected,
  getProjecsError,
  getProjectApi,

  postRecruitData,
  postRecruitFulfilled,
  postRecruitRejected,
  postRecruitError,

  toggleNested,
  setNestedMessage
) => {
  const [plannedProjects, setPlannedProjects] = useState([]);
  const myId = useSelector((state) => state.login.userId);
  const [alertData, alertAction] = useAlert();

  useEffect(() => {
    if (myId) {
      getProjectApi(myId);
    }
  }, [myId]);

  useEffect(() => {
    if (getProjectFulfilled) {
      // console.log(getProjectData);
      setPlannedProjects(getProjectData);
    }
  }, [getProjectFulfilled]);

  useEffect(() => {
    if (getProjectRejected) {
      if (getProjecsError) {
        alertAction.open(getProjecsError.response.data.message);
        console.log(getProjecsError);
      }
    }
  }, [getProjectRejected]);

  useEffect(() => {
    if (postRecruitFulfilled) {
      setNestedMessage({
        status: true,
        message: "   !",
      });
      toggleNested();
    }
  }, [postRecruitFulfilled]);

  useEffect(() => {
    if (postRecruitRejected) {
      if (postRecruitError) {
        console.log(postRecruitError);
        setNestedMessage({
          status: false,
          message: " .  .",
        });
        toggleNested();
      }
    }
  }, [postRecruitRejected]);
  return { plannedProjects };
};

export default useProfileRecruit;
